import React, { Component } from 'react';
import { Row, Col, Form, Select } from 'antd';
import { BtnSearch, BtnClear } from 'components/common/button';

const { Option } = Select;

@Form.create({ name: 'formScholarshipApproval' })
class SearchBar extends Component {
  state = {};

  handleReset = () => {
    this.props.form.resetFields();
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form className="ant-advanced-search-form" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <Col span={8}>
            <Form.Item label="H">
              {getFieldDecorator('system1', {})(
                <Select>
                  <Option value="1">i hc chnh quy</Option>
                  <Option value="2">Cao ng chnh quy</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Khoa">
              {getFieldDecorator('system2', {})(
                <Select>
                  <Option value="1">C-in t</Option>
                  <Option value="2">Qun tr kinh doanh</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Kha">
              {getFieldDecorator('system3', {})(
                <Select>
                  <Option value="1">Kha 1</Option>
                  <Option value="2">Kha 2</Option>
                  <Option value="3">Kha 3</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="CN">
              {getFieldDecorator('system4', {})(
                <Select>
                  <Option value="1">C kh</Option>
                  <Option value="2">in t vin thng</Option>
                  <Option value="3">in cng nghip</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Lp">
              {getFieldDecorator('system5', {})(
                <Select>
                  <Option value="1">KT1</Option>
                  <Option value="2">KT2</Option>
                  <Option value="3">KT3</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Hc k">
              {getFieldDecorator('system6', {})(
                <Select>
                  <Option value="1">Hc k 1</Option>
                  <Option value="2">Hc k 2</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Nm hc">
              {getFieldDecorator('system7', {})(
                <Select>
                  <Option value="1">2017-2018</Option>
                  <Option value="2">2018-2019</Option>
                  <Option value="3">2019-2020</Option>
                  <Option value="3">2021-2022</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="i tng tr cp">
              {getFieldDecorator('system8', {})(
                <Select>
                  <Option value="1">Sinh vin thuc h ngho</Option>
                  <Option value="2">Sinh vin ngho </Option>
                  <Option value="3">Sinh vin hiu hc</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={24} style={{ textAlign: 'right' }}>
            <div className="btn-group">
              <BtnSearch />
              <BtnClear />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default SearchBar;



